<template>
  <view class="maint-process padding-normal padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :background="background"
        title="维修流程"></u-navbar>

    <u-time-line>
      <u-time-line-item :bg-color="'#f2f2f8'" :node-top="'60'" v-for="(log, index) in logList" :key="index">
        <template v-slot:node>
          <u-icon name="info-circle" color="#24A27C" :size="24"></u-icon>
        </template>
        <template v-slot:content>
          <view class="log-item padding-normal bg-white radius-normal">
            <view class="item left" style="text-align: left;">
              <text>{{ log.content }}</text>
              <br>
              <text>{{ log.operTime }}</text>
            </view>
            <view class="item right" style="text-align: right;">
              <text v-if="log.operStaff">{{ log.operStaff }}</text>
              <br>
              <text v-if="log.operDept">{{ log.operDept }}</text>
            </view>
          </view>
        </template>
      </u-time-line-item>
    </u-time-line>
  </view>
</template>

<script>
  export default {
    name: "maint-process",
    data() {
      return {
        searchParams: {
          id: ""
        },
        background: {
          'background': '#f2f2f8'
        },
        logList: []
      }
    },
    onLoad(options) {
      this.searchParams.id = options.taskId
    },
    onReady() {
      this.getLogList();
    },

    methods: {
      getLogList() {
        this.$u
            .post("/maint/getLogList", this.searchParams)
            .then(res => {
              if (res.data) {
                this.logList = res.data;
              } else {
                this.empty = true;
              }
            })
            .catch(err => {
            });
      }
    }
  }
</script>

